.line1 {
  position: absolute;
  width: 32px;
  height: 2px;
  margin-top: 14px;
  margin-left: 8px;
  background-color: #202020;
  -webkit-animation: lines1;
          animation: lines1;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.line2 {
  position: absolute;
  width: 28px;
  height: 2px;
  margin-top: 22px;
  margin-left: 12px;
  background-color: #202020;
  -webkit-animation: lines2;
          animation: lines2;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.line3 {
  position: absolute;
  width: 24px;
  height: 2px;
  margin-top: 30px;
  margin-left: 16px;
  background-color: #202020;
  -webkit-animation: lines3;
          animation: lines3;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes lines1{
  from{margin-top: 22px;}
  to {margin-bottom: 0;}
}

@keyframes lines1{
  from{margin-top: 22px;}
  to {margin-bottom: 0;}
}

@-webkit-keyframes lines2{
  from{margin-top: 22px;}
  to {margin-top: 22px;}
}

@keyframes lines2{
  from{margin-top: 22px;}
  to {margin-top: 22px;}
}

@-webkit-keyframes lines3{
  from{margin-top: 22px;}
  to {margin-top: 30px;}
}

@keyframes lines3{
  from{margin-top: 22px;}
  to {margin-top: 30px;}
}

.xline1 {
  position: absolute;
  width: 32px;
  height: 2px;
  margin-top: 14px;
  margin-left: 8px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: #fff; 
  -webkit-animation-name: example3; 
          animation-name: example3;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.xline2 {
  position: absolute;
  width: 32px;
  height: 2px;
  margin-top: 14px;
  margin-left: 8px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  background-color: #fff;
  -webkit-animation-name: example2;
          animation-name: example2;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes example3{
  from {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
  to {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}

@keyframes example3{
  from {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
  to {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
}

@-webkit-keyframes example2{
  from {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
  to {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
}

@keyframes example2{
  from {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
  to {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}
}
